import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-event',
  templateUrl: './event.component.html',
  styleUrls: ['./event.component.scss']
})
export class EventComponent implements OnInit {
  @Input() eventObj;
  eventData;
  bgColor = "#a88bda";
  colors = ["#a88bda", "#6eb9f7", "#f1749e", "#f7776e"]
  selectedItem = 0;
  constructor(private router: Router) { }
  // eventObj = [
  //   {
  //     no: '11',
  //     msg: 'Order Due',
  //     bgColor: '#a88bda'
  //   },
  //   {
  //     no: '06',
  //     msg: 'Deliveries Today',
  //     bgColor: '#6eb9f7'
  //   },
  //   {
  //     no: '08',
  //     msg: 'Delayed Orders',
  //     bgColor: '#f1749e'
  //   },
  //   {
  //     no: '02',
  //     msg: 'Delayed Deliveries',
  //     bgColor: '#f7776e'
  //   }
  // ]

  ngOnInit() {
    this.setBackgroundColor(this.eventObj)
  }
  private setBackgroundColor(data) {
    if (data) {
      this.eventData = data.map((ele, index) => {
        ele["bgColor"] = this.colors[index]
        return ele;
      })
    }


  }
  clickOnTab(i) {
    this.bgColor = this.eventObj[i].bgColor;
    this.selectedItem = i;

  }
  check() {

    this.router.navigate(['/order-details'], { queryParams: { orderId: 12345678 } })

  }

}
